/*==========================================================================
  Base styles and utility classes
  ==========================================================================*/

:root {
    font-size: 16px;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px $yellow-bg;
    background-color: $yellow-bg;
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px $yellow-bg;
    border-radius: 10px;
    background-color: $yellow;
}

::-webkit-selection {
    background: $yellow;
    color: $white;
}

::-moz-selection {
    background: $yellow;
    color: $white;
}

::-ms-selection {
    background: $yellow;
    color: $white;
}

::selection {
    background: $yellow;
    color: $white;
}

.row {
    @media screen and (min-width: 90em) {
        max-width: 90em;
    }
}

.sticky {
    position: sticky;
}

.fixed {
    position: fixed;
}

.hidden {
    display: none;
}

.visible {
    display: block;
}

body {
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    color: $dark;
}

body.lock {
    overflow: hidden;
}

img {
    max-width: 100%;
}

address {
    font-style: normal;

    p {
        margin-bottom: 0px;
        line-height: 2em;
    }
}

.relative {
    position: relative;
}

.box-shadow {
    @include box-shadow();
}

.text-shadow {
    @include text-shadow();
}

.container {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    overflow-x: hidden;
}

.loader {
    width: 100%;
    position: relative;

    svg {
        width: 20%;
        position: absolute;
        left: 40%;
        top: 0;
    }
}

.info-message{
    position: relative;
    overflow: auto;
    width: 70%;
    margin: 20px auto;
    display: block;

    p{
        font-size: 2.5rem;
        line-height: 1.4;

        @media only screen and (max-width: 1024px){
            font-size: 1.4rem;
        }

        &.small{
            font-size: 1.8rem;
            @media only screen and (max-width: 1024px){
                font-size: 1rem
            }
        }
    }

    a{
        color: $yellow;
    }
}